@use 'sass:map';
@use '@angular/material' as mat;

@mixin color($theme) {
  .programs-table {
    & table {
      .table-rows:hover {
        background: mat.get-theme-color($theme, surface-container-low);
      }
    }

    .favorite-program-star {
      color: #fecb33;
    }
  }
}

// @mixin typography($theme) {
// }

@mixin theme($theme) {
  @include color($theme);
  // @include typography($theme);
}
