@use 'sass:map';
@use '@angular/material' as mat;

@mixin color($theme) {
  .basic-table-wrapper {
    background: mat.get-theme-color($theme, surface-container-low);

    .all-programs-input-wrapp {
      .basic-program-filter {
        background: mat.get-theme-color($theme, surface-container-lowest);
      }
    }

    .programs-table {
      .mat-basic-program-table {
        .table-rows:hover {
          background: mat.get-theme-color($theme, surface-container-high);
        }
      }
    }
  }
}

// @mixin typography($theme) {
// }

@mixin theme($theme) {
  @include color($theme);
  // @include typography($theme);
}
