@use 'sass:map';
@use '@angular/material' as mat;

@mixin color($theme) {
  .mat-drawer-container {
    background: mat.get-theme-color($theme, surface-container);
  }

  .filter-drawer {
    background: mat.get-theme-color($theme, surface-container-lowest);

    .drawer-wrapper {
      background: mat.get-theme-color($theme, surface-container-lowest);
    }
  }

  .mat-drawer-content {
    background: mat.get-theme-color($theme, surface-container-lowest);
  }

  mat-stepper {
    background: mat.get-theme-color($theme, surface-container-lowest);
  }

  .mat-step-header[aria-selected='true'] {
    background: mat.get-theme-color($theme, surface-container-high);

    // It should be tokenized with border-radius: --mat-stepper-header-hover-state-layer-shape: 12px;
  }

  .print-template-container {
    // & * {
    //   font-family: Roboto, sans-serif !important;
    // }

    .program-client-container {
      .info-wrapper {
        span:first-of-type {
          font-size: 18px;
          font-weight: 500;
        }

        span {
          font-size: 18px;
          font-weight: 400;
        }
      }
    }

    .print-grid-table {
      .exercise-cell {
        background-color: white;
        // border: 2px solid black;

        .title {
          border-bottom: 1px solid black;

          & > span {
            font-size: 14px;
          }
        }

        .exercise-info-wrapper {
          border-top: 1px solid black;

          .sets-reps {
            // line-height: 2;
            // border-bottom: 1px solid black;

            & span {
              font-size: 14px;
            }

            & > div:first-of-type {
              // border-right: 1px solid black;
            }
          }

          .notes {
            & > span {
              font-size: 14px;
            }
          }
        }
      }
    }
  }

  // Print template
  @media print {
    .print-template-container {
      & * {
        font-family: Roboto, sans-serif !important;
      }

      .program-client-container {
        .info-wrapper {
          span:first-of-type {
            font-size: 18px;
            font-weight: 500;
          }

          span {
            font-size: 18px;
            font-weight: 400;
          }
        }
      }

      .print-grid-table {
        .exercise-cell {
          background-color: white;
          // border: 2px solid black;

          .title {
            border-bottom: 1px solid black;

            & > span {
              font-size: 14px;
            }
          }

          .exercise-info-wrapper {
            border-top: 1px solid black;

            .sets-reps {
              // line-height: 2;
              border-bottom: 1px solid black;

              & span {
                font-size: 14px;
              }
            }

            .notes {
              & > span {
                font-size: 14px;
              }
            }
          }
        }
      }
    }
  }
}

// @mixin typography($theme) {
// }

@mixin theme($theme) {
  @include color($theme);
  // @include typography($theme);
}
