@media screen and (max-width: 1048px) {
  .flex-container {
    .inputs-wrapper {
      flex-direction: column;
      height: calc(100vh - 132px);
      overflow-y: scroll;

      .left-input-column {
        flex: 1;
        order: 2;
      }

      .right-input-column {
        flex: 1;
        order: 1;
        height: auto;
        overflow-y: visible;
      }
    }
  }
}
