@use 'sass:map';
@use 'sass:color';
@use '@angular/material' as mat;

@mixin color($theme) {
  $theme-color: mat.get-theme-color($theme, surface-container-highest);

  .selected-exercises {
    background: mat.get-theme-color($theme, surface-container-low);
    color: mat.get-theme-color($theme, on-surface);

    .add-exercise-placeholder {
      color: mat.get-theme-color($theme, outline);
    }
  }

  .search-segmented-btns {
    background: mat.get-theme-color($theme, surface-container-lowest);
  }

  .edit-client-name {
    color: mat.get-theme-color($theme, outline);
  }

  .edit-program-name {
    color: mat.get-theme-color($theme, outline);
  }

  .exercise-card-placeholder {
    background: mat.get-theme-color($theme, surface-container-highest);
  }

  .exercise-card-preview {
    border: 1px solid mat.get-theme-color($theme, surface-container-highest);
  }

  .selected-cards-box {
    // background: mat.get-theme-color($theme, surface-container-high);
    background: mat.get-theme-color($theme, surface);
    // color: rgba(0, 0, 0, 0.87);
    border-bottom: solid 1px mat.get-theme-color($theme, surface-dim);
  }

  // .grid-container-placeholder {
  //   .exercise-cards-placeholder {
  .exercise-card-skeleton {
    background: mat.get-theme-color($theme, surface-container-low);

    &::after {
      background-image: linear-gradient(
        90deg,
        color.adjust($theme-color, $alpha: -1) 0%,
        color.adjust($theme-color, $alpha: -0.8) 20%,
        color.adjust($theme-color, $alpha: -0.5) 60%,
        color.adjust($theme-color, $alpha: -1)
      );
    }
  }
  //   }
  // }

  .background-wrapper-placeholder {
    .main-wrap-placeholder {
      background: mat.get-theme-color($theme, surface-container-low);

      &::after {
        background-image: linear-gradient(
          90deg,
          color.adjust($theme-color, $alpha: -1) 0%,
          color.adjust($theme-color, $alpha: -0.7) 20%,
          color.adjust($theme-color, $alpha: -0.4) 60%,
          color.adjust($theme-color, $alpha: -1)
        );
      }
      // background-color: red;
    }
  }
}

// @mixin typography($theme) {
// }

@mixin theme($theme) {
  @include color($theme);
  // @include typography($theme);
}
