@use 'sass:map';
@use '@angular/material' as mat;

@mixin color($theme) {
  .wrap-default {
    border: 1px solid mat.get-theme-color($theme, surface-container-high);

    .card {
      .card-head {
        // background: mat.get-theme-color($theme, surface-container-lowest);
        background: #ffffff;
      }

      .card-body {
        background: mat.get-theme-color($theme, surface-container-high);

        .card-icons {
          .favorite-exercise-star {
            color: #fecb33;
          }
        }
      }
    }

    &:hover {
      //   box-shadow: 1px 2px 4px mat.get-theme-color($theme, primary);
      outline-offset: 2px;
      outline: 2px solid mat.get-theme-color($theme, inverse-primary);
    }
  }

  //   .wrap-selected {
  //     .card {
  //       .card-head {
  //         background-color: #ffffff;
  //       }
  //     }
  //   }
}

// @mixin typography($theme) {
// }

@mixin theme($theme) {
  @include color($theme);
  // @include typography($theme);
}
