@use 'sass:map';
@use 'sass:color';
@use '@angular/material' as mat;

@mixin color($theme) {
  $theme-color: mat.get-theme-color($theme, surface-container-highest);

  .clients-program-list-table {
    .clients-table {
      tr.client-program-element-row:not(.client-program-expanded-row):hover {
        background: mat.get-theme-color($theme, surface-container-low);
      }

      tr.client-program-element-row:not(.client-program-expanded-row):active {
        background: mat.get-theme-color($theme, surface-container-low);
      }

      .client-program-element-detail {
        .table-client-program-wrapper {
          .list-item {
            border-bottom: 2px solid
              mat.get-theme-color($theme, surface-container);

            &:hover {
              background: mat.get-theme-color($theme, surface-container-low);
            }

            .list-item-flex {
              .list-info {
                .list-info-details {
                  color: mat.get-theme-color($theme, outline);
                }
              }
            }
          }
        }
      }
    }
  }

  .status-chip-wrapper {
    .status-chip {
    }

    .status-completed {
      background: mat.get-theme-color($theme, secondary-container);
      color: mat.get-theme-color($theme, on-secondary-container);
    }

    .status-inprogress {
      background: mat.get-theme-color($theme, error-container);
      color: mat.get-theme-color($theme, on-error-container);
    }
  }

  .basic-programs-table-placeholder {
    background: mat.get-theme-color($theme, surface-container-low);

    .animate-skeleton {
      &::after {
        background-image: linear-gradient(
          90deg,
          color.adjust($theme-color, $alpha: -1) 0%,
          color.adjust($theme-color, $alpha: -0.8) 20%,
          color.adjust($theme-color, $alpha: -0.5) 60%,
          color.adjust($theme-color, $alpha: -1)
        );
      }
    }
  }
}

// @mixin typography($theme) {
// }

@mixin theme($theme) {
  @include color($theme);
  // @include typography($theme);
}
