@use 'sass:map';
@use 'sass:color';
@use '@angular/material' as mat;

@mixin color($theme) {
  $theme-color: mat.get-theme-color($theme, surface-container-highest);

  .inputs-wrapper {
    .right-input-column {
      background: mat.get-theme-color($theme, surface-container-low);
    }
  }

  .upload-image-placeholder {
    background: mat.get-theme-color($theme, surface-container-low);

    .upload-image-skeleton {
      &::after {
        background-image: linear-gradient(
          90deg,
          color.adjust($theme-color, $alpha: -1) 0%,
          color.adjust($theme-color, $alpha: -0.8) 20%,
          color.adjust($theme-color, $alpha: -0.5) 60%,
          color.adjust($theme-color, $alpha: -1)
        );
      }
    }
  }
}

// @mixin typography($theme) {
// }

@mixin theme($theme) {
  @include color($theme);
  // @include typography($theme);
}
