@use 'sass:map';
@use '@angular/material' as mat;

@mixin color($theme) {
  .upload-image {
    .file-upload-button-wrapper {
      background: mat.get-theme-color($theme, surface-container-low);

      .file-upload-button {
        background: mat.get-theme-color($theme, surface-container-high);

        &:hover {
          background: mat.get-theme-color($theme, surface-container-highest);
        }
      }
    }

    .remove-wrapper {
      .file-upload-button {
        background: mat.get-theme-color($theme, error);
        color: mat.get-theme-color($theme, on-error);
        

        &:hover {
        background: mat.get-theme-color($theme, on-error-container);

        }
      }
    }
  }
}

// @mixin typography($theme) {
// }

@mixin theme($theme) {
  @include color($theme);
  // @include typography($theme);
}
