@use 'sass:map';
@use 'sass:color';
@use '@angular/material' as mat;

@mixin color($theme) {
  $theme-color: mat.get-theme-color($theme, surface);

  .fetchingDataSpinner {
    background: color.adjust($theme-color, $alpha: -0.7);
  }
}

// @mixin typography($theme) {
// }

@mixin theme($theme) {
  @include color($theme);
  // @include typography($theme);
}
