@use 'sass:map';
@use '@angular/material' as mat;
@use './styles/themes/all' as sanareapp-theme;
// @use './styles/themes/all-light' as sanareapp-theme;
@use './styles/components';
@use './styles/util/custom_webkit-scrollbars.scss';
@import 'overlayscrollbars/overlayscrollbars.css';

@include mat.core();

:root {
  // @include mat.all-component-themes(sanareapp-theme.$light-theme);

  // Include only styles for used components
  @include mat.core-theme(sanareapp-theme.$light-theme);

  @include mat.button-theme(sanareapp-theme.$light-theme);
  @include mat.card-theme(sanareapp-theme.$light-theme);
  @include mat.progress-bar-theme(sanareapp-theme.$light-theme);
  @include mat.tooltip-theme(sanareapp-theme.$light-theme);
  @include mat.form-field-theme(sanareapp-theme.$light-theme);
  @include mat.input-theme(sanareapp-theme.$light-theme);
  @include mat.select-theme(sanareapp-theme.$light-theme);
  @include mat.dialog-theme(sanareapp-theme.$light-theme);
  @include mat.chips-theme(sanareapp-theme.$light-theme);
  @include mat.slide-toggle-theme(sanareapp-theme.$light-theme);
  @include mat.menu-theme(sanareapp-theme.$light-theme);
  @include mat.list-theme(sanareapp-theme.$light-theme);
  @include mat.paginator-theme(sanareapp-theme.$light-theme);
  @include mat.tabs-theme(sanareapp-theme.$light-theme);
  @include mat.checkbox-theme(sanareapp-theme.$light-theme);
  @include mat.icon-button-theme(sanareapp-theme.$light-theme);
  @include mat.fab-theme(sanareapp-theme.$light-theme);
  @include mat.snack-bar-theme(sanareapp-theme.$light-theme);
  @include mat.table-theme(sanareapp-theme.$light-theme);
  // @include mat.table-color(themes.$light-theme); // [TEMP]Change to variables

  @include mat.progress-spinner-theme(sanareapp-theme.$light-theme);
  @include mat.button-toggle-theme(sanareapp-theme.$light-theme);
  @include mat.datepicker-theme(sanareapp-theme.$light-theme);
  @include mat.divider-theme(sanareapp-theme.$light-theme);
  @include mat.expansion-theme(sanareapp-theme.$light-theme);
  @include mat.icon-theme(sanareapp-theme.$light-theme);
  @include mat.sidenav-theme(sanareapp-theme.$light-theme);
  @include mat.stepper-theme(sanareapp-theme.$light-theme);
  @include mat.sort-theme(sanareapp-theme.$light-theme);
  @include mat.toolbar-theme(sanareapp-theme.$light-theme);
  @include mat.tree-theme(sanareapp-theme.$light-theme);

  @include components.theme(sanareapp-theme.$light-theme);

  // @include mat.autocomplete-theme(sanareapp-theme.$light-theme);
  // @include mat.radio-theme(sanareapp-theme.$light-theme);
  // @include mat.slider-theme(sanareapp-theme.$light-theme);
  // @include mat.badge-theme(sanareapp-theme.$light-theme);
  // @include mat.bottom-sheet-theme(sanareapp-theme.$light-theme);
  // @include mat.grid-list-theme(sanareapp-theme.$light-theme);
  // Include system variables
  // @include mat.system-level-colors(sanareapp-theme.$light-theme);
  // @include mat.system-level-typography(sanareapp-theme.$light-theme);
  @include mat.typography-hierarchy(sanareapp-theme.$light-theme);

  @include mat.color-variants-backwards-compatibility(
    sanareapp-theme.$light-theme
  );
}

// Temporary, until all styles are refactored to material design tokens
// Tables background
.mat-table-background {
  --mat-table-background-color: #ffffff;
}

// Paginators background
.mat-paginator-background {
  --mat-paginator-container-background-color: #ffffff;
}

// Basic programs table
.mat-basic-program-table {
  --mat-table-background-color: #f3f3fa;
}
.mat-basic-program-paginator {
  --mat-paginator-container-background-color: #f3f3fa;
}

// Remove underline in exercises searc input when no focus
.search-input .mat-mdc-text-field-wrapper {
  --mdc-filled-text-field-active-indicator-height: 0px;
}

