@use 'sass:map';
@use '@angular/material' as mat;

@mixin color($theme) {
  .edit-program-flex {
    .content-wrapper {
      background: mat.get-theme-color($theme, surface-container-low);
    }
  }

  .exer-expansion-panel {
    // color: mat.get-theme-color($theme, error);

    .expansion-panel-handle {
      color: mat.get-theme-color($theme, outline);
    }
  }
}

// @mixin typography($theme) {
// }

@mixin theme($theme) {
  @include color($theme);
  // @include typography($theme);
}
