@use 'sass:map';
// @use 'sass:color';
@use '@angular/material' as mat;

@mixin color($theme) {
  // $border-color: mat.get-theme-color($theme, surface-container);
  // $transparent-border-color: color.scale($border-color, $alpha: 60%);

  .confirmation-dialog-wrapper {
    border: 2px solid mat.get-theme-color($theme, surface-container-high);
  }
  .dialog-content-wrapper {
    .category-labels {
      .category-value {
        // color: #000;
      }

      .chips-category {
        // background-color: #dbe2f9;
      }
    }
  }

  .label {
    color: mat.get-theme-color($theme, outline);
  }
}

// @mixin typography($theme) {
